.card-container {
    background-color: #2b2b2b;
}

.card {
    background-color: #2b2b2b;
    display: flex;
    padding: 2rem;
    justify-content: center;
    margin: 1rem;
  }
  
  .card img {
    max-height: 100%;
  }
  
  .card-main {
    margin: 0 1rem 0 2rem;
  }
  
  .card p {
    color: white;
    max-width: 35ch;
    line-height: 2;
    margin-bottom: 2rem;
  }
  
  .card h1 {
    font-size: 2rem;
    color: white;
    margin-bottom: 1rem;
  }
  
  .card-info {
    display: flex;
    flex-direction: column;
    height: 83%;
    width: 100%;
  }
  
  .card-links {
    display: flex;
  }
  
  @media screen and (max-width: 1150px) {
    .card-links {
      margin-top: 2rem;
    }
  }
  
  @media screen and (max-width: 900px) {
    .card {
      flex-direction: column;
      align-items: center;
    }
  
    .card img {
      margin: 2rem 0;
    }
  
    .card p {
      margin-bottom: 2rem;
    }
  
    .card-info {
      width: 100%;
    }
  
    .card-links {
      margin-top: 2rem;
    }
  }